/* App.css */

body {
  font-family: Arial, sans-serif;
  background-color: #f4f4f4;
  margin: 0;
  padding: 20px;
  color: #333;
}

.App {
  max-width: 600px;
  margin: auto;
  background: white;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

h1 {
  color: #5D647B;
}

input[type="text"] {
  padding: 10px;
  font-size: 16px;
  border: 2px solid #ddd;
  width: calc(100% - 22px);
  box-sizing: border-box;
}

button {
  padding: 10px 20px;
  background-color: #5D647B;
  color: white;
  border: none;
  font-size: 16px;
  cursor: pointer;
  float: right;
}

button:hover {
  background-color: #4E5571;
}

ul {
  list-style: none;
  padding: 0;
}

li {
  padding: 10px;
  border-bottom: 1px solid #ddd;
  display: flex;
  align-items: center;
}

li:last-child {
  border-bottom: none;
}

input[type="checkbox"] {
  margin-right: 10px;
}

span {
  flex-grow: 1;
}

span.completed {
  text-decoration: line-through;
  color: #888;
}

